<template>
  <div class="page">
    <xheader is-left="true" title=""></xheader>
    <div class="box">
      <mt-field label="" placeholder="" v-model="ApplicationData.userName"></mt-field>
      <mt-field label="" placeholder="" type="number" v-model="ApplicationData.cardNumber"></mt-field>
      <mt-field label="" placeholder="" type="password" v-model="ApplicationData.withdrawalSum"></mt-field>
    </div>
    <div class="footer" @click="ApplicationCashFun">
      
    </div>
  </div>
</template>

<script>

  import xheader from "./public/xheader"

  import {Toast} from "mint-ui"

  export default {
    name: "ApplicationPresentation",
    components: {
      xheader
    },
    data() {
      return {
        ApplicationData: {
          withdrawalSum: "",
          cardNumber: "",
          userName: "",
        }
      }
    },
    methods: {
      ApplicationCashFun() {
        let $self = this
        let reg = /\d{15}|\d{19}/
        if (reg.test($self.ApplicationData.cardNumber)) {
          $self.$store.dispatch("ApplicationCash", this.ApplicationData)
            .then(response => {
              if (response.success) {
                Toast("")
              } else {
                Toast("" + response.result)
              }
            })
            .catch(error => {
              Toast("" + error)
            })
        } else {
          Toast("")
        }
      }
    }
  }
</script>

<style scoped lang="scss">

  .footer {
    height: 50px;
    line-height: 50px;
    text-align: center;
    color: #fff;
    /*text-justify: auto;*/
    font-size: 16px;
    background: #00aaef;
  }
</style>



