/*  */


const group = {
  state: {
    RoomList: [],  //
    RoomPermissions: 0,    // 
  },
  mutations: {
    SET_RoomList(state, list) {
      state.RoomList = list
    },
    SET_RoomPermissions(state, str) {
      state.RoomPermissions = str
    }
  }
}

export default group



