const cordovaPlugin = {
  state: {

    isListen: false,     // cordova deviceready  

    isQrcode: false,   //  
    _badgeNum: "0"      // 
  },
  mutations: {
    SET_isQrcode(state, str) {
      state.isQrcode = str
    },
    SET_badgeNum(state, str) {
      state._badgeNum = str
    },
    SET_isListen(state, str) {
      state.isListen = str
    }
  },
  actions: {}
}

export default cordovaPlugin



