<template>
  <div class="page">
    <xheader is-left="true" title=""></xheader>
     <div class="box">
       <div v-if="liveRoomMsg.rstatus === '0'">
          
       </div>
       <div class="container">
         <div class="player">
           <video-player class="video-player vjs-custom-skin"
                         ref="videoPlayer"
                         :playsinline="true"
                         :options="playerOptions"
                         @play="onPlayerPlay($event)"
                         @pause="onPlayerPause($event)"
           >
           </video-player>
         </div>
       </div>
       <div v-if="liveRoomMsg.rstatus==='2'">
         
       </div>
     </div>

  </div>
</template>

<script>
  import xheader from "../components/public/xheader"
  import Qs from "qs"
  import md5 from "js-md5"
  import {videoPlayer} from 'vue-video-player'
  import {Indicator} from "mint-ui"

  export default {
    name: "liveRoom",
    data() {
      return {
        liveRoomMsg: {},
        playerOptions: {
//        playbackRates: [0.7, 1.0, 1.5, 2.0], //
          autoplay: false, //true,
          muted: false, // 
          loop: false, // 
          preload: 'auto', // <video>auto,
          language: 'zh-CN',
          aspectRatio: '16:9', //  - "16:9""4:3"
          fluid: true, // trueVideo.js player
          sources: [{
            type: "application/x-mpegURL",
            src: "http://vatimebank.com:81/hls/test.m3u8" //m3u8
          }],
          // poster: "poster.jpg", //
          width: document.documentElement.clientWidth,
          notSupportedMessage: '', //Video.js
//        controlBar: {
//          timeDivider: true,
//          durationDisplay: true,
//          remainingTimeDisplay: false,
//          fullscreenToggle: true  //
//        }
        }
      }
    },
    components: {
      xheader,
      videoPlayer
    },
    methods: {
      queryLiveRoom() {
        Indicator.open()
        let $self = this
        let timestamp = Date.parse(new Date())
        let data = Qs.stringify({
          userId: $self.$store.state.login.userId,
          token: $self.$store.state.login.token,
          timestamp: timestamp + "",
          sign: md5($self.$store.state.login.token + timestamp),
          roomId: $self.$route.params.liveId
        })

        $self.$http.post($self.$config.bizserverApi + "/liveservice/selectlivebyid", data)
          .then(response => {
            if (response.data.success) {
              $self.liveRoomMsg = response.data.result;
              $self.playerOptions.sources[0].src = response.data.result.pullHlsUrl
              // console.log("" + $self.playerOptions)
              Indicator.close()
            }
          })
      },


      onPlayerPlay(player) {
        // alert("play");
      },
      onPlayerPause(player) {
        // alert("pause");
      },
    },
    computed: {
      player() {
        return this.$refs.videoPlayer.player
      }
    },
    created() {
      this.queryLiveRoom()
    }
  }
</script>

<style lang="scss">
  .box {
    .my_video-dimensions {
      width: 100%;
      height: 752px;
    }
    .container {
      background-color: #efefef;
      min-height: 100%;
    }
  }
</style>



